.offerContainer{
    display: flex;
}

.activeOffer {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background-color: green;
    margin: auto;
}

.notActiveOffer {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background-color: red;
    margin: auto;
}